<template>
  <v-container class="px-3 pa-0 ma-0" fluid v-if="true">
    <div class="font-weight-medium px-0 mb-3" :class="!showDrawer ? 'mobile-title' : 'desk-title'">
      Мое расписание
    </div>
    <hr>
    <div class="text-lg-h6 text-md-h5 text-sm-h4 text-xs-h1 px-0 mb-3">
      Ваши текущие занятия
    </div>
    <v-row>
      <v-col class="pa-0">
        <router-view/>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  components: {},
  mounted() {
    this.test = true
  },
  data: () => ({
    test: false,
    showDrawer: true,
  }),
  methods: {
    checkWindowWidth() {
      this.showDrawer = window.innerWidth >= 1260;
    },
  },

  created() {
    this.checkWindowWidth();
    window.addEventListener('resize', this.checkWindowWidth);
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.checkWindowWidth);
  },
}
</script>

<style lang="scss" >
@import "@/assets/styles/buttonStyles.css";
@import "@/assets/styles/lineStyles.css";
.mobile-title {
  font-size: 32px;
}
.desk-title {
  font-size: 40px;
}
</style>